
















































import Vue from 'vue';
import Playlist from './components/Playlist.vue';

import '../../styles/PlaylistDrawer.scss';

export default Vue.extend({
  name: 'PlaylistDrawer',
  components: { Playlist },
  props: {
    drawer: Boolean,
    futurePlaylistItems: {
      type: Array,
      default: () => [],
    },
    historyPlayListItems: {
      type: Array,
      default: () => [],
    },
  },
  data: () => ({
    tab: 0,
  }),
  methods: {
    replay(index: number) {
      this.$emit('replay', index);
    },
    cutIn(index: number) {
      this.$emit('cutIn', index);
    },
  },
});
